<template>
  <Spinner v-if="!tracks"/>
  <v-row v-else>
    <Track
      v-for="track in tracks"
      :key="track.track_id"
      :track="track.track"
    />
  </v-row>
</template>

<script>
import Track from '@/components/Track.vue'
import Spinner from '@/components/Spinner.vue'
export default {
  components: {
    Track,
    Spinner
  },
  computed: {
    tracks() {
      return this.$store.getters.tracks
    }
  },
  mounted() {
    this.$store.dispatch('fetchTopTracks')
  },
}
</script>
