<template>
  <v-card class="mx-auto mb-10" outlined>
    <v-list-item three-line>
      <v-list-item-content>
        <v-list-item-title class="headline mb-1" align="center"
          >Search For A Song</v-list-item-title
        >
        <v-list-item-subtitle align="center">Get the lyrics for any song</v-list-item-subtitle>
        <v-form ref="form" @submit.prevent="handleSubmit">
          <v-text-field v-model="trackTitle" label="Song Title..." required />
          <v-btn block color="blue" type="submit">
            Get Track Lyrics
          </v-btn>
          {{trackTitle}}
        </v-form>
      </v-list-item-content>
    </v-list-item>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    trackTitle: '',
  }),
  methods: {
    handleSubmit() {
      this.$store.dispatch('searchTrack', this.trackTitle)
      this.trackTitle = ''
    }
  }
}
</script>
